import { GET_NAME } from "./type"
import { getConfig } from "@/api"
export default {
   async getConfig({commit,state}) {
        let res = await getConfig()
        console.log(res);
        if(res.code == 0) {
            commit(GET_NAME,res.data)
        }
    }
}


