</template>

<script>
    import {
        Tabbar,
        TabbarItem
    } from 'vant';
    import Vue from "vue"
    Vue.use(Tabbar).use(TabbarItem);
    export default {
        data() {
            return {
                active: 0,
                
            }
        },
        created() {
            
        }
    
    }
</script>

<style lang="stylus">

