
import  axios  from 'axios';

if (process.env.NODE_ENV === 'product' && process.env.VUE_APP_CURRENTMODE === 'daily') {
    axios.defaults.baseURL = 'https://api-daily.huazhukuaixin.com';
} else if (process.env.NODE_ENV === 'product') {
    axios.defaults.baseURL = 'https://api.huazhukuaixin.com';
}
axios.defaults.timeout = 30 * 1000;
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

// 添加请求拦截器
axios.interceptors.request.use((config) => {
    // if (window.sessionStorage._csrf_token_) {
    //     axios.defaults.headers.common['_csrf_token_'] = window.sessionStorage._csrf_token_;
    // }
    // 在发送请求之前做些什么
    return config;
}, (error) => {
    // 对请求错误做些什么
    return Promise.reject(error);
});

// 添加响应拦截器
axios.interceptors.response.use((response) => {
    // console.log( window.sessionStorage._csrf_token_,"axios")

    // 对响应数据做点什么
    return response;
}, (error) => {
    // 对响应错误做点什么
    return Promise.reject(error);
});


function checkStatus(response) {
     // 如果http状态码正常，则直接返回数据
     if (response && (response.status === 200 || response.status === 304 || response.status === 400)) {
            return response.data;
    }
    // 异常状态下，把错误信息返回去
    return {
        status: response.status,
        msg: '异常'
    };
}
function checkCode(error) {
    // console.log(error.request.status);
        // 如果code异常(这里已经包括网络错误，服务器错误，后端抛出的错误)，可以弹出一个错误提示，告诉用户
        if (error.status === -404) {
            alert(error.msg);
        }
        if (error.data && (!error.data.success)) {
            alert(error.data.error_msg);
        }
        return error;
}


export default  {
    get(url,data){
       return axios.get(url,data)
        .then(function(response){
            return checkStatus(response);
        }).catch(function(error){
            return checkCode(error);
        })
    },
    post(url,data) {
       return axios.post(url,data)
        .then(function(response){
            return checkStatus(response);
        }).catch(function(error){
            return checkCode(error);
        })
    }
}





